.wave {
  width: 200%;
  height: 100%;
  position: relative;
  /* background: linear-gradient(to right, #d3e4f0 0%, #4ba0ff 25%, #44ff00 50%, #f036af 75%, #0099ff 100%); */
  transform: translateX(-50%);
