/* Animation on Hover */
/* .i:hover {
    animation-name: zoom-in-out;
    animation-duration: 1s;
    animation-direction: alternate;
  } */
  .image:hover{
    animation-name: zoom-in-out;
    animation-duration: 1s;
    animation-direction: alternate;
  }
  
  /* Animation on Load */
  img {
    animation-name: fade-in;
    animation-duration: 1s;
  }
  
  @keyframes zoom-in-out {
    from {
      transform: scale(1);
    }
    to {
      transform: scale(1.2);
    }
  }
  
  @keyframes fade-in {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  } 
  


